          </van-col>
          <!-- <van-col span="13">
            <span class="rightTxt"
              >{{ $t("message.f_5") }}{{ coinBalance | price }}</span
            ></van-col> -->
        </van-row>
        <div class="put">
          <van-row type="flex" justify="space-between">
            <van-col span="20" class="topPledge">
              <input
                type="number"
