                v-model="stakeAmount"
                @input="getNbn($event, 2)"
                onkeyup="this.value=this.value.replace(/[^0-9]/g,'')"
              />
            </van-col>
            <van-col span="4" class="textRight"
              ><button @click="max">MAX</button></van-col
            >
            <!-- <van-col span="8"
              ><div class="bg_logo" style="margin-left: 10px">
                <img src="../../assets/img/usdt.png" alt="" /> <span>USDT</span>
              </div></van-col
            > -->
          </van-row>
        </div>
      </div>
      <button class="btn" @click="lpApprove" v-if="!isApprove">
        {{ $t("message.f_6") }} USDT
      </button>
      <div class="textCenter">
        <img src="../../assets/img/1-1.png" alt="" width="28" />
      </div>
      <div class="inputBox">
        <van-row type="flex" justify="space-between" align="center">
          <van-col span="12" class="topPledge">
            <div class="bgWhite flex">
              <div class="bg_logo">
                <img
                  src="../../assets/img/usdt.png"
                  alt=""
                  width="40"
                />
                <span>USDT（30%）</span>
              </div>
            </div>
          </van-col>
          <van-col span="12"
            ><span class="rightTxt"
              >{{ $t("message.f_5") }}{{ coinBalance | price }}</span
            ></van-col
          >
        </van-row>
        <div class="put">
          <van-row type="flex" justify="space-between">
            <van-col span="16" class="topPledge">
              <span class="">{{stakeAmount*0.3}}</span>
            </van-col>
          </van-row>
        </div>
      </div>
        <div class="textCenter">
        <img src="../../assets/img/1.png" alt="" width="28" />
      </div>
       <div class="inputBox">
        <van-row type="flex" justify="space-between" align="center">
          <van-col span="12" class="topPledge">
            <div class="bgWhite flex">
              <div class="bg_logo">
                <img
                  src="../../assets/img/logo1.png"
                  alt=""
                  width="40"
                />
                <span>AES（70%）</span>
              </div>
            </div>
          </van-col>
          <van-col span="12"
            ><span class="rightTxt"
              >{{ $t("message.f_5") }}{{ aesBalance ? aesBalance.balance:'0'| price4 }}</span
            ></van-col
          >
        </van-row>
        <div class="put">
          <van-row type="flex" justify="space-between">
            <van-col span="16" class="topPledge">
              <input
                type="text"
                placeholder="0"
                disabled="disabled"
                v-model="nbn"
              />
            </van-col>
          </van-row>
        </div>
      </div>
      <div class="prci">
        <van-row type="flex" justify="space-between">
          <van-col span="12"><span style="color:#4bb5ed">AES{{ $t("message.dao_4") }}：${{DaoInfo?DaoInfo.AESPrice:'0'}}</span></van-col>
          <van-col span="12" style="    text-align: right;color: #ffa0ae;" v-if="DaoInfo&&(Number(DaoInfo.AESPrice)<25)"><span>{{ $t("message.dao_5") }}：4%</span></van-col>
          <van-col span="12" style="    text-align: right;color: #ffa0ae;" v-if="DaoInfo&&(25<=Number(DaoInfo.AESPrice))&&(Number(DaoInfo.AESPrice)<50)"><span>{{ $t("message.dao_5") }}：3%</span></van-col>
          <van-col span="12" style="    text-align: right;color: #ffa0ae;" v-if="DaoInfo&&(50<=Number(DaoInfo.AESPrice))&&(Number(DaoInfo.AESPrice)<100)"><span>{{ $t("message.dao_5") }}：2%</span></van-col>
          <van-col span="12" style="    text-align: right;color: #ffa0ae;" v-if="DaoInfo&&(100<=Number(DaoInfo.AESPrice))&&(Number(DaoInfo.AESPrice)<500)"><span>{{ $t("message.dao_5") }}：1%</span></van-col>
        </van-row>
      </div>

        <!-- <button class="btn" @click="stake" v-if="isFiveApprove">
          {{ $t("message.f_7") }}
        </button>
        <button class="btn" @click="FiveApprove" v-else>
          {{ $t("message.f_6") }} AES
        </button> -->
      <p style="color: #ffa0ae;">*{{ $t("message.dao_6") }}</p>
    </div>

    <div class="cent">
      <h1 class="textCenter title">{{ $t("message.dao_7") }}<span>{{ $t("message.dao_8") }}</span></h1>
        <div class="ming_bix">
          <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="12">
              <span>{{ $t("message.dao_9") }}(USDT)</span>
              <h2>{{ DaoInfo ? DaoInfo.info[3] : "0" | price4 }}</h2>
            </van-col>
            <van-col span="12">
              <span>{{ $t("message.dao_10") }}(USDT)</span>
              <h2>{{ DaoInfo ? (Number(DaoInfo.info[3])*2-Number(DaoInfo.info[8])) : "0" | price4 }}</h2>
            </van-col>
          </van-row>
          <van-row gutter="20" type="flex" justify="space-between">
            <van-col span="12">
              <!-- <span>{{ $t("message.f_29_1") }}</span> -->
              <span>{{ $t("message.dao_11") }}(USDT)</span>
              <h2 v-if="DaoInfo">{{DaoInfo ? DaoInfo.info[8] : "0" | price4}} <br/><small>≈
               {{DaoInfo ? DaoInfo.info[9] : "0"  | priceFive}} FIVE</small></h2>
            </van-col>
            <van-col span="12">
              <span>{{ $t("message.dao_12") }}(USDT)</span>
              <h2 v-if="DaoInfo">
                {{DaoInfo ? DaoInfo.info[26] : "0" | price4}}
              <br/><small>≈
             {{DaoInfo ?  DaoInfo.info[27] : "0" | priceFive}} FIVE</small></h2>
            </van-col>
          </van-row>
          <button class="btn" @click="withdrawFIVEfun">{{ $t("message.dao_13") }}</button>
        </div>

      <h1 class="textCenter title" v-if="list.length>0">{{ $t("message.dao_7") }}<span>{{ $t("message.dao_14") }}</span></h1>
      <div class="my" v-if="list.length>0">
        <ul class="tabTop">
          <li class="w30">{{ $t("message.dao_15") }}<br/>(USDT)</li>
          <li class="w10">{{ $t("message.dao_16") }}</li>
          <li class="w30">{{ $t("message.dao_17") }}<br/>(USDT)</li>
          <li class="w30">{{ $t("message.dao_18") }}<br/>(USDT)</li>
        </ul>
        <ul class="tabCen" v-for="(itme ,index) in list" :key="index">
          <li class="w30">{{(itme.investAmount / 10**18)}}</li>
          <li class="w10">{{itme.apy}}%</li>
          <li class="w30">{{(itme.earnAmount / 10**18)}}</li>
          <li class="w30">{{( Number(itme.maxAmount) - Number(itme.earnAmount)) | price4}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      isFiveApprove: false,
      stakeAmount: null,
      withdrawAmount: "",
      timer: null,
      timer2: null,
      show: false,
      isHome: true,
      nbn: 0,
      isApproveGrcc: false,
      showCionList: false,
      cionType: "five",
      list:[],
    };
  },
  computed: {
    ...mapState("main", [
      "hmdInfo",
      "account",
      "balance",
      "mxtCoinContract",
      "mxtContract",
      // "hmdBalance",
      // "FiveCoinContract",
      // "GCoinContract",
      // "GBalance",
      "aesBalance",
       "DaoContract",
      "DaoInfo",
      "FiveDead",
      "aesCoinContract",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      // "add_price",
      "add_hmdInfo",
      "add_mxtCoinContract",
      "add_hmdCoinContract",
      // "add_gCoinContract",
      "add_aesCoinContract",
      "add_DaoContract"
    ]),

    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    getList:function(){
      if (this.DaoContract == null) {
        
        return;
      }
      this.list = [];
      		var _this = this;
			var investsLength = _this.DaoInfo.info[13];
			var times = parseInt(investsLength) - 1;
       var account = _this.account;
        //  console.log('times=========',investsLength)
			for (let i = times; i > -1; i--) {
         console.log(times)
			  _this.DaoContract.methods
        .getInvestInfo(account, i)
        .call()
        .then((res) => {
							var investAmount =res[1];	//投资的金额
							var apy = res[6]
							var earnAmount = res[3];	//当前收益
							var maxAmount = res[2];
            
          _this.list.push({
									times: i,
									investAmount: investAmount,
									apy: apy,
									earnAmount: earnAmount,
									maxAmount: maxAmount,
								});
                _this.list.sort(_this.compare('times'));
         
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });

			}

        

// _this.seedHistory = true;
			// _this.beginTimer()
    },
  compare:function(val) {
	      return function (a, b) {
	      var value1 = a[val];
	      var value2 = b[val];
	      return  value2 -value1 ;
	      }
	   },
    getNbn(e, type) {
      if(!this.DaoInfo) return
      var val = e.target.value;
      var AESusdt = val*0.7;
      var aes = Number(AESusdt)/Number(this.DaoInfo.AESPrice);
      return  this.nbn = aes.toFixed(4);
      if (type == 1) {
        var val = e;
      } else {
        var val = e.target.value;
      }
      if (!e) return;
      if (this.cionType == "five") {
        var Price2 = this.hmdInfo.fivePrice;
      } else {
        var Price2 = this.hmdInfo.GrccPrice;
      }
      var prex = 4;
      var price = Number(Price2) * Number(val);

      // info.fivePrice =null;
      if (parseInt(price) == price) {
        this.nbn = price;
        return;
      }
      let len = 1;
      let i = 0;
      while (parseInt(price) != price) {
        price = price * 10;
        len *= 10;
        i++;
      }
      let last = Math.abs(i <= prex ? 0 : price % Math.pow(10, i - prex));
      let d1 = price - last;
      if (last > 0) {
        d1 = d1 + Math.pow(10, i - prex);
      }
      d1 = d1 / len;
      this.nbn = d1;
    },
    max: function () {
      var num = parseInt(this.coinBalance);
      var prec = 2;
      var ceil = true;
      console.log(num)
      const len = String(num).length;
      if (len <= prec) {
        return  this.$toast(this.$t("message.yu_1"));
      }

      const mult = Math.pow(10, prec);
      this.stakeAmount = Math.floor(num / mult) * mult;
      console.log(num,this.stakeAmount)
      this.getNbn(this.stakeAmount, 1);
    },
    stake: function () {
      if (this.DaoContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      // var inviteCode = _this.hmdInfo.inviteCode.toString();
      // var referrer = _this.hmdInfo.referrer.toString();
      var account = _this.account;

      if (!_this.isApprove) {
        _this.$toast(_this.$t("message.noAccredit"));
        return;
      }

      if (Number(_this.stakeAmount) % 100 > 0) {
        _this.$toast(_this.$t("message.yu_1"));
        return;
      }
      console.log(Number(_this.stakeAmount)*0.3>60.8524,'000');
      if (Number(_this.stakeAmount)*0.3 > Number(_this.coinBalance)) {
        _this.$toast(_this.$t("message.noBanner"));
        return;
      }

      if (Number(_this.balance / 10 ** 18) < 0) {
        _this.$toast(_this.$t("message.gas"));
        return;
      }
      if (Number(_this.stakeAmount) <= 0) {
        _this.$toast(_this.$t("message.noNum"));
        return;
      }
      if (Number(_this.stakeAmount) < 100) {
        _this.$toast(_this.$t("message.f_67_1"));
        return;
      }
      var monye = (_this.stakeAmount * 10 ** 18).toString();
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
   
      console.log(
        "investIn=======",
        web3.utils.toWei(_this.stakeAmount.toString(), "ether")
      );
      this.DaoContract.methods
        .investIn(
          web3.utils.toWei(_this.stakeAmount.toString(), "ether")
        )
        .send({
          from: account,
        })
        .then((res) => {
          console.log("investIn", res);
          _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee"));
          // _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
            _this.add_DaoContract();
            setTimeout(function(){
               _this.getList();
            },1000)

          _this.stakeAmount = "";
          _this.nbn = "";
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    changeAppLocale(locale) {
      this.changeLanguage(locale);
    },
    // Five授权
    FiveApprove: function () {
      if (this.aesCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.DaoInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.aesCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isFiveApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    FiveisApproveFun: function () {
      var account = this.account;
      var address = this.DaoInfo.address;

      this.aesCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isFiveApprove = true;
          }
        });
    },

    // USDT授权
    lpApprove: function () {
      if (this.mxtContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = this.DaoInfo.address;
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.mxtCoinContract.methods
        .approve(address, "10000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权
    isApproveFun: function () {
      var account = this.account;
      var address = this.DaoInfo.address;

      this.mxtCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log("usdt=========isApproveFun", res);
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.mxtCoinContract) {
        var account = this.account;
        this.mxtCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
    
    withdrawFIVEfun: function () {
      if (this.DaoContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var _this = this;
      var account = _this.account;
      // var vipAvgBonus = web3.utils.fromWei(_this.DaoInfo.info[6], "ether");
      //   if (vipAvgBonus <= 0) {
      //     this.$toast(this.$t("message.bonusMsg"));
      //     return;
      //   }
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.DaoContract.methods
        .withdrawProfit()
        .send({ from: account })
        .then((res) => {
         _this.$toast.clear();
          _this.$toast.success(_this.$t("message.dealSuccee2"));
          // _this.add_hmdInfo();
          _this.getCoinBalance();
          _this.add_hmdCoinContract();
            _this.add_DaoContract();
            setTimeout(function(){
               _this.getList();
            },1000)
        })
        .catch((err) => {
          _this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
  },
  created() {
    this.add_mxtCoinContract();
    this.getCoinBalance();
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "en");
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }
    var getData = () => {
      this.add_hmdInfo();
      this.add_mxtCoinContract();
      this.add_hmdCoinContract();
      this.add_aesCoinContract();
      this.add_DaoContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.mxtCoinContract || !this.DaoInfo) {
          getData();
        } else {
          this.getCoinBalance();
          this.isApproveFun();
          this.FiveisApproveFun();
          if(_this.list.length<1){
          this.getList();
          }
  //  
          // console.log(25<=3&&3<25)
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();
// setTimeout(function(){
//     _this.getList();
//     console.log(_this.list)
// },2000)
     
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.info {
  background: url(../../assets/img/bg1.png) top center no-repeat,
    url(../../assets/img/bg2.png) center bottom no-repeat;
  background-size: 100%, 100%;
  padding-top: 5em;
  .top {
    text-align: center;
    padding: 30px;
    width: 370px;
    height: 188px;
    box-sizing: border-box;
    background: url(../../assets/img/info/top_bg.png) top center no-repeat;
    background-size: 100%;
    margin: 5px auto 20px;
    box-sizing: border-box;
    .sl {
      margin-bottom: 10px;
    }
    span {
      color: #eee;
      font-family: PingFangSC-Regular, PingFang SC;
      font-size: 12px;
    }
    h2 {
      font-size: 18px;
      margin-top: 4px;
      margin-bottom: 12px;
      font-family: DINAlternate-Bold, DINAlternate;
      font-weight: bold;
      color: #ffffff;
    }
  }

  .title {
    font-size: 26px;
