<template>
  <div class="home">
    <HeadTop  :isHome="isHome" :lang="lang" @changeAppLocale="changeAppLocale"/>
    <!-- <HeadTop
      @changeAppLocale="changeAppLocale"
      :type="type"
      :isHome="isHome"
      v-bind:lang="lang"
    /> -->
    <div class="transfer">
       <h1 class="textCenter title">{{ $t("message.dao_19") }}<span>{{ $t("message.dao_20") }}</span> </h1>
       <p class="tips">{{ $t("message.dao_21") }}</p>
      <div class="pledge">
        <h3>{{ $t("message.dao_22") }}</h3>
        <div class="inputBox">
          <van-row type="flex" justify="space-between">
            <van-col span="8" class="topPledge">
                <!-- <span>{{ $t("message.f_4") }}</span> -->
                <div class="bgWhite flex">
                <div class="bg_logo"><img src="../../assets/img/logo1.png" alt="" width="17"> <span style="font-size:12px">AES</span></div>
              </div>
            </van-col>
            <van-col span="14"><span class="rightTxt">{{ $t("message.f_5") }}{{coinBalance | price}}</span></van-col>
          </van-row>
          <div class="put">
             <van-row type="flex" justify="space-between">
              <van-col span="20" class="topPledge">
                  <input type="number" placeholder="0" v-model="allNum" />
              </van-col>
              <van-col span="4" class="textRight"><button @click="max">MAX</button></van-col>
            </van-row>
          </div>
        </div>
        <p class="transforTiip">*{{ $t("message.dao_23") }}</p>
        <button class="btn" @click="lpApprove" v-if="!isApprove" >{{ $t("message.f_6") }} AES</button>
          <div class="textCenter"><img src="../../assets/img/1-1.png" alt="" width="28"></div>
          <h3>{{ $t("message.dao_24") }}</h3>
         <div class="inputBox">
          <textarea type="text" v-model="transferList" placeholder="0x..."></textarea>
        </div>
        <p class="transforTiip">*{{ $t("message.dao_25") }}</p>
         <button class="btn" @click="transferFun" >{{ $t("message.f_7") }} </button>
        
      </div>


    </div>
  </div>
</template>

<script type="es6">
import HeadTop from "../../components/Header";
import FontCon from "../../components/Footer";
import { mapActions, mapState } from "vuex";
import Web3 from "web3";
export default {
  data() {
    return {
      lang: "en",
      currency: "",
      link: "--",
      coinBalance: 0,
      isApprove: false,
      allNum:null,
      timer: null,
      timer2: null,
      show: false,
      isHome:true,
       transferList:null,
    };
  },
  computed: {
    ...mapState("main", [
      "aesCoinContract",
      "account",
      "transferContract",
    ]),
  },
  components: {
    HeadTop,
    FontCon,
  },
  methods: {
    ...mapActions("main", [
      "add_aesCoinContract",
      "add_transferContract",
    ]),
    protectAddress: function (addr, num) {
      if (addr) {
        var newAddr = addr.replace('"', "").replace('"', "");
        if (newAddr == "0x0000000000000000000000000000000000000000") {
          return "--";
        }
        if (!newAddr) {
          return "--";
        }
        if (newAddr == 1) {
          return newAddr;
        }
        return (newAddr + "").length == 42
          ? newAddr.substring(0, num) + "****" + newAddr.substring(39, 42)
          : newAddr;
      }
    },
    changeLanguage(lang) {
      this.$i18n.locale = lang;
      this.lang = lang;
    },
    max: function () {
       var num=this.coinBalance;
      num = num.toString();
      let index = num.indexOf(".");
      if (index !== -1) {
        num = num.substring(0, 4 + index + 1);
      } else {
        num = num.substring(0);
      }
      this.allNum = num;
    },

    changeAppLocale(locale) {
      this.changeLanguage(locale);

      // this.$router.push({
      //   name: this.$route.name,
      //   params: { lang: this.lang },
      //   query: { r: localStorage.getItem("r") },
      // });
    },

    // AES授权
    lpApprove: function () {
      if (this.aesCoinContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
      var address = '0x988647358e8edb5e1e981835668fb84bca273551';
      var account = this.account;
      this.$toast.loading({
        // message: this.$t("message.pack"),
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      this.aesCoinContract.methods
        .approve(address, "5000000000000000000000000000")
        .send({ from: account })
        .then((res) => {
          console.log("approve", res);
          this.$toast.clear();
          this.$toast.success(this.$t("message.accredit"));
          this.isApprove = true;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    // 是否授权AES
    isApproveFun: function () {
      var account = this.account;
      var address = '0x988647358e8edb5e1e981835668fb84bca273551';

      this.aesCoinContract.methods
        .allowance(account, address)
        .call()
        .then((res) => {
          console.log('usdt=========isApproveFun',res)
          var a = web3.utils.fromWei(res, "ether");
          if (a > 0) {
            this.isApprove = true;
          }
        });
    },
      transferFun(){
      var len;
    
       if (this.transferContract == null) {
        this.$toast(this.$t("message.noMask"));
        return;
      }
        if (!this.allNum) {
        this.$toast(this.$t("message.f5_38"));
        return;
      }
        if (Number(this.allNum) > Number(this.coinBalance)) {
        this.$toast(this.$t("message.f5_40"));
        return;
      }
       if (!this.transferList) {
        this.$toast(this.$t("message.f5_39"));
        return;
      }
        if(this.transferList){
        var len = this.transferList.split("\n");
      }
      var _this = this;
      var account = _this.account;
      this.$toast.loading({
        message: 'loading',
        duration: 0,
        overlay: true,
        closeOnClickOverlay: true,
      });
      console.log(len,this.transferList,_this.allNum*10**9)
      // return
      this.transferContract.methods
        .promo(len,  web3.utils.toWei(_this.allNum.toString(), "ether"))
        .send({ from: account })
        .then((res) => {
            _this.$toast.clear();
         _this.$toast.success(_this.$t("message.f5_16"));
         this.ArrList=null;
        })
        .catch((err) => {
          this.$toast.clear();
          this.$toast(err.message);
          console.log(err);
        });
    },
    //获取当前币种余额
    getCoinBalance: function () {
      if (this.aesCoinContract) {
        var account = this.account;
        this.aesCoinContract.methods
          .balanceOf(account)
          .call()
          .then((res) => {
            var num = web3.utils.fromWei(res, "ether");
            num = num.toString();
            let index = num.indexOf(".");
            if (index !== -1) {
              num = num.substring(0, 6 + index + 1);
            } else {
              num = num.substring(0);
            }
            this.coinBalance = num;
          });
      }
    },
  },
  created() {
      this.add_aesCoinContract();
      this.getCoinBalance();
  },
  mounted() {
    var _this = this;
    this.changeLanguage(localStorage.getItem("lang") || "en");
    if (this.$route.query.invite) {
      localStorage.setItem("invite", this.$route.query.invite);
    }
    var getData = () => {
      this.add_aesCoinContract();
      this.add_transferContract();
      // this.add_hmdCoinContract();
      // this.add_gCoinContract();
    };

    var getDataCalculate = () => {
      getData();
      this.timer = setInterval(() => {
        if (!this.aesCoinContract ) {
          getData();
        } else {
          this.getCoinBalance();
          this.isApproveFun();
          clearInterval(this.timer);
        }
      }, 1000);
    };
    getDataCalculate();
  
    // 每三十秒，刷新一次
    this.timer2 = setInterval(() => {
      if (this.timer) {
        clearInterval(this.timer);
        this.timer = null;
      }
      getDataCalculate();
    }, 80000);
  },
  destroyed() {
    if (this.timer) {
      clearInterval(this.timer);
      this.timer = null;
    }
    if (this.timer2) {
      clearInterval(this.timer2);
      this.timer2 = null;
    }
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped  lang="less">
.home{
 background: url(../../assets/img/bg1.png)top center no-repeat,url(../../assets/img/bg2.png)center bottom no-repeat;
 background-size: 100%,100%;
}
.transfer{padding-top: 80px;
.tips{
      font-size: 13px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    line-height: 22px;
    color: #666666;
    text-align: center;
}
}
.pledge{
  // background: url(../../assets/img/put.png)top center no-repeat;
  // background-size: 100%;
  background: linear-gradient(#FFF3F5,#FFFFFF,#FFFFFF);
  box-shadow: 0 2px 18px #77acf7;
  width: 88%;
  box-sizing: border-box;
  margin: 25px  auto;
  padding: 30px 20px;
  border-radius: 30px;
  textarea{
    min-height: 160px;
    font-size: 14px !important;
    line-height: 20px !important;
    font-weight: normal !important;
  }
        input,textarea{
        font-size: 20px;
        font-family: SourceHanSansCN-Bold, SourceHanSansCN;
        font-weight: bold;
        color: #323335;
        line-height: 30px;
        width: 100%;
        box-sizing: border-box;
        border: none;
           background: #f0f4fb;
    padding: 5px 12px;
      }
  h3{
font-size: 16px;
font-family: PingFangSC-Regular, PingFang SC;
font-weight: 400;
color: #4DA9DB;
line-height: 28px;
margin: 5px;
  }
  .transforTiip{margin: 15px 10px;
    color: #999;}
  .inputBox{
    margin: 0px 0;
    padding:10px 10px 8px;
    background: #FFFFFF;
    border-radius: 12px;
    border: 1px solid #E8E8E8;
    span{line-height: 28px;text-transform: uppercase;}
    .topPledge{
      display: flex;color: #999;
      position: relative;
    .jiao{
      width: 10px;
      margin-left: 5px;
    }
    .img01{  
    animation: rotate 6s linear infinite;
}
      ul{
        position: absolute;
        background: #f4f7fe;
        min-width: 68px;
        padding: 10px;
        z-index: 2;
        border-radius: 5px;
        margin-top: 3px;
        li{
          display: flex;
          align-items: center;
          border-bottom: 1px solid #e7e7e7;
          padding: 5px 0;
          span{color: #666;font-size: 12px;}
          img{vertical-align: middle;margin-right: 5px;}
        }
      }
    }
    .bg_logo{
       display: flex;
       min-width: 55px;
    /* height: 28px; */
    /* line-height: 28px; */
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: #323335;
    background: #F4F7FE;
    border-radius: 4px;
    padding: 0 8px;
    align-items: center;
    img{
          width: 18px;
          margin-right: 5px;
    }
    }
    .rightTxt{color: #999;text-align: right;display: block;}
    .put{
      display: flex;
      margin-top: 20px;

      button{border: none;background: none;color:#4DA9DB;padding: 5px 0 0;}
    }
  }
  .btn{
    width: 100%;
    margin: 20px auto;
    display: block;
      background: url(../../assets/img/btn.png)top center no-repeat;
  background-size: 100%;
  border: none;
  text-align: center;
  color:#fff;
  font-family: PingFangSC-Medium, PingFang SC;
font-weight: 500;
font-size: 17px;
padding: 1em 0 1.5em;
  }
}

  .title{
     font-size: 26px;
font-family: SourceHanSansCN-Bold, SourceHanSansCN;
font-weight: bold;
color: #4DA9DB;
line-height: 39px;
margin: 20px 0 5px;
span{
  color: #FFA0AE;
}
  }
  .imgAll{
    padding:80px 3% 80px;
    position: relative;
    background: url(../../assets/img/3.png)bottom center no-repeat;
    background-size: 100%;
    img{
      margin-bottom: 5px;
    }
  }
  .imgAll::before{
    content: '';
    display: block;
    width: 56px;
    height: 85px;
    background: url(../../assets/img/2.png)top center no-repeat;
    background-size: 100%;
    position: absolute;
    left: 0;
    top: -33px;
  }
  @keyframes rotate{
	0%{
		transform: rotateZ(0deg);/*从0度开始*/
	}
	100%{
		transform: rotateZ(360deg);/*360度结束*/
	}
}
</style>



